<template>
 <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
      </crumb>
    </div>
    <div class="center_content">
        <!-- <div class="center_content_title">
            <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
            <span>{{pageTitle}}</span>
        </div> -->
        <div class="center_content_search">
            <div class="center_content_search_left">
                <div class="search-input-suffix">
                    <label>字典搜索：</label>
                    <el-input class="search"
                            @keyup.enter.native="search"
                            v-model="page.keywords"
                            placeholder="请输入搜索关键词"
                            clearable
                            >
                    </el-input>
                    <!-- <el-button @click="search" type="primary">搜索</el-button> -->
                </div>
                <div class="search-input-suffix" style="margin: 0 0 0 20px">
                  <div class="search--btn"  @click="search">搜索</div>
                </div>
            </div>
            <!-- <div class="center_content_search_right">
                <el-button  @click="openAdd" type="primary" icon="el-icon-plus" round>新增</el-button>
            </div> -->
        </div>
         <div class="table-container">
             <el-table
                :data="tableData"
                stripe
                style="width: 100%">
                <el-table-column
                prop="name"
                label="字典名称">
                </el-table-column>
                <el-table-column
                prop="field"
                label="字段名">
                </el-table-column>
                <el-table-column
                prop="code"
                label="代码">
                </el-table-column>
                <el-table-column
                prop="value"
                label="字典值">
                </el-table-column>
                <el-table-column
                label="操作"
                width="160">
                <template slot-scope="scope">
                    <div class="operate-button distance">
                        <SvgIcon icon-class="iconbianji1"></SvgIcon>
                        <el-button
                        @click.native.prevent="editRow(scope.row)"
                        type="text"
                        size="small">
                        编辑
                        </el-button>
                    </div>
                    <div class="operate-button">
                        <SvgIcon icon-class="iconshanchu"></SvgIcon>
                        <el-button
                        @click.native.prevent="deleteRow(scope.row)"
                        type="text"
                        size="small"
                        style="color: #ff5858">
                        删除
                        </el-button>
                    </div>                    
                </template>
                </el-table-column>
            </el-table>
            <div class="center_pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.rows"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="data.total">
                </el-pagination>
            </div>
        </div>
    </div>
     <!-- 新增编辑 -->
    <el-dialog :title="dialogTitle" :visible.sync="userDialogFormVisible" :before-close="handleDialogClose" class="set-el-dialog" :close-on-click-modal='false'>
        <el-form :model="form" :rules="rules" ref="form" label-width="120px">
            <el-form-item label="字典名称" prop="name">
            <el-input v-model="form.name"  placeholder=""></el-input>
            </el-form-item>
             <el-form-item label="字段名" prop="field">
            <el-input v-model="form.field"  placeholder=""></el-input>
            </el-form-item>
             <el-form-item label="代码" prop="code">
            <el-input-number v-model="form.code" controls-position="right" :min="0" :max="9999"></el-input-number>
            </el-form-item>
             <el-form-item label="字典值" prop="value">
            <el-input v-model="form.value"  placeholder=""></el-input>
            </el-form-item>
             <el-form-item label="排序" prop="sort">
            <el-input-number v-model="form.sort" controls-position="right" :min="0" :max="9999"></el-input-number>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button  @click="cancel('form')">取 消</el-button>
            <el-button type="primary" @click="create('form')">确 定</el-button>
        </div>
    </el-dialog>
 </div>
</template>

<script>
import systemApi from '@/api/systemApi'
export default {
 name: 'DictionaryManaged',
 data() {
 return {
    pageTitle:'字典管理',
    data:{},
    tableData: [],
    userDialogFormVisible:false,
    isAdd:true,
    form: {
        name: '',
        field: '',
        code: '',
        value: '',
        sort: '',
        id:undefined
    },
    page:{
        keywords:'',
        page:1,
        rows:15
    },
    rules:{
        name: [
            {
              required: true,
              message: '请输入字典名称',
              trigger: 'blur'
            }
        ],
        field: [
            {
              required: true,
              message: '请输入字段名',
              trigger: 'blur'
            }
        ],
        code: [
            {
              required: true,
              message: '请输入代码',
              trigger: 'blur'
            }
        ],
        value: [
            {
              required: true,
              message: '请输入字典值',
              trigger: 'blur'
            }
        ],
        sort: [
            {
              required: true,
              message: '请输入排序',
              trigger: 'blur'
            }
        ]
    }
 }
 },
 created(){},
 mounted(){
     this.initData()
 },
 computed:{
     dialogTitle(){
        return this.isAdd ? '新增字典' : '编辑字典'
     }
 },
 methods:{
     initData(){
         const url =systemApi.dictList;
         this.$post(url,this.page).then(res =>{
             if(res.status === 1){
                 this.data=res;
                 this.tableData=res.data;                 
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: res.message
                })
             }
         }).catch(err =>{
           console.log(err)
         })
     },
      headInfo(item){
        this.pageTitle=item[1]
     },
     search(){
        this.resetTemp();
        this.initData() ;
     },
     handleSizeChange(val){
        this.page.rows = val;
		this.initData()
     },
     handleCurrentChange(obj){
        this.page.page = obj;
        this.initData()
     },
     openAdd(){         
          this.userDialogFormVisible = true;
          this.form={
                name: '',
                field: '',
                code: '',
                value: '',
                sort: '',
                id:undefined
        };       
        this.isAdd=true;
     },
   deleteRow(row){
        this.$confirm('确定删除该字典?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
           this.userDel(row.id)
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
        });        
     },
     editRow(row){
        const obj={
                name: row.name,
                field: row.field,
                code: row.code,
                value: row.value,
                sort: row.sort,
                id:row.id
        }
        this.form=obj;
        this.userDialogFormVisible = true; 
        this.isAdd=false;
     },
     create(formName){            
       this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.isAdd){
                  this.userAdd()
            }else{   
                 this.userEdit()             
                
            }           
                     
          } 
        });
     },
     cancel(formName){
        this.userDialogFormVisible = false;
        this.$refs[formName].resetFields();
     },
     handleDialogClose(){
        this.$refs['form'].resetFields();
        this.userDialogFormVisible = false;    
     },
     userAdd(){
        const url =systemApi.dictAdd;
       this.$post(url,this.form)
            .then( res => {
                    if(res.status === 1){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                        this.userDialogFormVisible = false;
                        this.resetTemp()
                        this.initData() ;
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                    }
                    
            }).catch(err =>{
                 console.log(err)
            })
     },
     userDel(id){
       const url =systemApi.dictDel({
           id:id
       });
        this.$deletes(url)
        .then( res => {
                if(res.status === 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: res.message
                        
                        })
                    this.initData() ;
                }else{
                    this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.message
                })
                }
                
        }).catch(err =>{
             console.log(err)
        })
     },
     userEdit(){
        const url =systemApi.dictEdit;
        this.$put(url,this.form)
            .then( res => {
                    if(res.status === 1){
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: res.message
                            
                            })
                        this.userDialogFormVisible = false;
                        this.initData() ;
                    }else{
                        this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                    }
                    
            }).catch(err =>{
                console.log(err)
            })
     },
    resetTemp(){
        this.page.page=1;
        this.page.rows=15;
     }
    
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss'
</style>



